import React from "react"
import ReactDOM from "react-dom"
import { Card, CardBody, Input, Button, } from "reactstrap"
import { AgGridReact } from "ag-grid-react"
import { ContextLayout } from "../../utility/context/Layout"
// import { ChevronDown } from "react-feather"
import axios from "axios"
import {connect} from "react-redux"
import Customize from "./CustomizeDeals"
import {type} from "./type";

import Breadcrumbs from "../../components/@vuexy/breadCrumbs/BreadCrumb"

import "../../assets/scss/plugins/tables/_agGridStyleOverride.scss"
import "../../assets/scss/plugins/tables/_customAgGridStyleOverride.scss"
import "../../assets/scss/pages/app-email.scss"

class Deals extends React.Component {
  state = {
    rowData: null,
    paginationPageSize: 20,
    currenPageSize: "",
    getPageSize: "",
    defaultColDef: {
      sortable: true,
      editable: false,
      resizable: true,
      suppressMenu: true
    },
    columnDefs: [],
    dealType: {},
    pinnedBottomRowData: [],
    composeMailStatus: false,
  }

  componentDidMount() {
    axios.get("/api/aggrid/data").then(response => {
      // let rowData = response.data.data // replace for product
      let tempdata = {
        closed_dms: "DMS Status", // dms state
        date_sold: "09-02-2014",
        lender: "Credit Concepts",
        client_name: "Rottgers",
        stock: 25923,
        dealid: 2, // dealid
        vehicle: "2007 BMW 328xi",
        amount_financed: "$9570.00",
        discount: "$0.00",
        sales_team: "Doug Stephen",
        deskteam: "Mike Blake",

        deal: "teste - 121221",
        sales: "Brent",
        status: "Paid",
        log: "Log",
        due_date: "02/08/2020",
        amount: "$30",
        payment_method: "Cash",
        action: "Mark Unpaid"
      };

      let rowData = [];
      for (let i = 0; i < 50; i++) {
        rowData.push(Object.assign({}, tempdata));
      }

      JSON.stringify(rowData)
      this.setState({
        rowData,
      });
    })
  }
  UNSAFE_componentWillReceiveProps(props) {
    this.initTable(props);
  }
  UNSAFE_componentWillMount() {
    this.initTable(this.props);
  }
  componentDidUpdate() {
      this.changePageTitle()
  }

  initTable(props) {
    if(this.state.dealType.dealName === props.match.params.type) {
      return;
    }

    let dealType = type.find((deal) => {
      return deal.dealName === props.match.params.type
    });
    let pinnedBottomRowData = [];

    if(props.match.params.type === "deferred_payments") {
      pinnedBottomRowData.push({deal: "Total: 50", status: "Due Today: $0.00", log: "Past Due: $0.00", amount: "Over Due: $0.00", payment_method: "Total: $508.00"});
    } else {
      pinnedBottomRowData.push({lender: "Total", amount_financed: "$76858.00", discount: "$150.00", stock: "18"});
      pinnedBottomRowData.push({lender: "Average", amount_financed: "$4269.89", discount: "$8.33"});
    }

    this.setState({
      dealType,
      columnDefs: dealType.columnDefs,
      pinnedBottomRowData,
      composeMailStatus: false
    });
  }

  onGridReady = params => {
    this.gridApi = params.api
    this.gridColumnApi = params.columnApi
    this.setState({
      currenPageSize: this.gridApi.paginationGetCurrentPage() + 1,
      getPageSize: this.gridApi.paginationGetPageSize(),
      totalPages: this.gridApi.paginationGetTotalPages()
    })
  }

  updateSearchQuery = val => {
    this.gridApi.setQuickFilter(val)
  }

  filterSize = val => {
    if (this.gridApi) {
      this.gridApi.paginationSetPageSize(Number(val))
      this.setState({
        currenPageSize: val,
        getPageSize: val
      })
    }
  }
  handleCustomize() {
    this.handleComposeSidebar("open")
    this.onSetSidebarOpen(false)
  }
  handleComposeSidebar = status => {
    if (status === "open") {
      this.setState({
        composeMailStatus: true
      })
    } else {
      this.setState({
        composeMailStatus: false
      })
    }
  }

  onSetSidebarOpen = open => {
    this.setState({ sidebarOpen: open })
  }
  handleMainAndComposeSidebar = () => {
    this.handleComposeSidebar("close")
    this.onSetSidebarOpen(false)
  }
  changePageTitle =() => {
    ReactDOM.render(
        "Deal Management-Funding Tracker",
        document.getElementsByTagName("title")[0]
    );
  }

  render() {
    const { rowData, columnDefs, defaultColDef, pinnedBottomRowData } = this.state
    const { pageName } = this.state.dealType
    return (
      <React.Fragment>
        <div className="email-application">
          <Breadcrumbs
            breadCrumbTitle={pageName}
            breadCrumbParent="Deal Management"
            breadCrumbActive={pageName}
          />
          <Customize 
            handleComposeSidebar={this.handleComposeSidebar}
            currentStatus={this.state.composeMailStatus}
            // currentDealStatus={}
          />
          <Card className="overflow-hidden agGrid-card" style={{height: "100% !important"}} onClick={this.handleMainAndComposeSidebar}>
            <CardBody className="py-0" style={{height: "100% !important"}}>
              {this.state.rowData === null ? null : (
                <div className="ag-theme-material w-100 my-2 ag-grid-table" style={{height: "100% !important",width: "100% !important"}}>
                  <div className="d-flex flex-wrap justify-content-end align-items-center">
                    <div className="mb-1">
                    </div>
                    <div className="d-flex flex-wrap justify-content-between mb-1">
                      <div className="table-input mr-1">
                        <Input
                          placeholder="search..."
                          onChange={e => this.updateSearchQuery(e.target.value)}
                          value={this.state.value}
                        />
                      </div>
                      <div className="customize-btn">
                        <Button.Ripple
                          block
                          color="primary"
                          onClick={() => this.handleCustomize()}
                        >
                          Customize
                        </Button.Ripple>
                      </div>
                      <div className="export-btn">
                        <Button.Ripple
                          color="primary"
                          onClick={() => this.gridApi.exportDataAsCsv()}
                        >
                          Export as CSV
                        </Button.Ripple>
                      </div>
                    </div>
                  </div>
                  <ContextLayout.Consumer>
                    {context => (
                      <AgGridReact
                        gridOptions={{}}
                        rowSelection="multiple"
                        defaultColDef={defaultColDef}
                        columnDefs={columnDefs}
                        rowData={rowData}
                        onGridReady={this.onGridReady}
                        // colResizeDefault={"shift"}
                        // animateRows={true}
                        floatingFilter={true}
                        pagination={true}
                        // paginationPageSize={this.state.paginationPageSize}
                        // pivotPanelShow="always"
                        enableRtl={context.state.direction === "rtl"}
                        pinnedBottomRowData={pinnedBottomRowData}
                        rowHeight="40"
                      />
                    )}
                  </ContextLayout.Consumer>
                </div>
              )}
            </CardBody>
          </Card>
        </div>
      </React.Fragment>
    )
  }
}
const mapStateToProps = state => {
  return {
    app: state.calendar
  }
}

export default connect(mapStateToProps)(Deals)
